<template>
  <div id="login" class="auth">
    <div class="wrap">
      <div class="title">LOGIN</div>
      <form id="form" v-on:submit.prevent="login()">
          <div class="form-group">
              <label for="username">Username</label>
              <input type="text" id="username" placeholder="   ">
          </div>
          <div class="form-group">
              <label for="password">Password</label>
              <input type="password" id="password" placeholder="  ">
          </div>
          <div class="btn-group">
              <input class="btn" type="submit" value="SIGN IN">
              <input class="btn" type="button" onclick="location.href = '/auth/register'" value="SIGN UP">
          </div>
      </form>
      <a href="/" class="btn-home">Home @ usicMusic</a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      token: ''
    }
  },
  methods: {
    login: function () {
      const username = document.getElementById('username').value
      const password = document.getElementById('password').value
      if (username === '') {
        this.$js.alert('  ', null, this.$js.Icons.Warning, '')
      } else if (password === '') {
        this.$js.alert(' ', null, this.$js.Icons.Warning, '')
      } else {
        this.$http.post('/api/auth/login', {username, password})
          .then((result) => {
            this.token = result.data.token
            localStorage.setItem('token', this.token)
            localStorage.setItem('username', username)
            this.$js.alert(` ${username}`, ' ', this.$js.Icons.Success, '').then(() => {
              location.replace('/')
            })
          }).catch(error => {
            console.log(error)
            this.$js.alert('   ', ' ', this.$js.Icons.Warning, '')
          })
      }
    }
  }
}
</script>

<style scoped>
</style>



